      (function() {
          window.addEventListener('load', function(event) {
             fetch ('./php/getUser.php')
                .then(response=>{
                return response.text();
                })
               .then(text => {
                   if (text != "") {
                     document.querySelector(".ch-box").innerHTML=text;
                   }   
               }) 
            
          }, false);
      })();  